export default {
    "name": 'tabMaxAuto',
    props: ["navlist"],
    data(){
        return {
            tabCurrentIndex: 0
        }
    },
    methods: {
        //获取订单列表
        loadData(source){
            this.$emit("loadData")
        },
        changeTab(e){
            this.tabCurrentIndex = e.target.current;
            this.$emit("changeTab", this.tabCurrentIndex)
        },
        tabClick(index){
            this.$emit("tabClick", index)
            this.tabCurrentIndex = index;
        },
    },
    created(options){
    },
}
